
.au-play-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    color: inherit;
    pointer-events: auto;
    cursor: pointer;
    * {
        cursor: pointer;
    }
    .bi {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 50px;
        margin-left: 7px;
    }
}


.au-play-btn__btn {
    border: none;
    outline: none;
    background: transparent;
    color: inherit;
    padding: 0;
    margin: 0;
    position: absolute;
}

.au-play-btn__circle {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.outerCircle {
    position: absolute;
    background-color: transparent;
    border: 6px solid rgba(255, 255, 255, .5);
    opacity: .9;
    border-right: 6px solid transparent;
    border-left: 6px solid transparent;
    border-radius: 100px;
    width: 112px;
    height: 112px;
    margin: 0 auto;
    animation: spinPulse 3s infinite ease-in-out;
}

.innerCircle {
    position: absolute;
    background-color: transparent;
    border: 5px solid rgba(255, 255, 255, 0.75);
    opacity: .9;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    border-radius: 100px;
    width: 92px;
    height: 92px;
    margin: 0 auto;
    animation: spinoffPulse 3s infinite linear;
}


@keyframes spinPulse {
    0% {
        transform: rotate(160deg);
        opacity: 0;
        box-shadow: 0 0 1px #bdd73c;
    }

    50% {
        transform: rotate(145deg);
        opacity: 1;
    }

    100% {
        transform: rotate(-320deg);
        opacity: 0;
    }
}

@keyframes spinoffPulse {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

@keyframes rotatecircle {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(-360deg);
    }
}
