
.k-game-content-area {
    display: flex;
    flex-direction: column;
    height: 100%;
    pointer-events: all;
}

.k-game-content-area__instruction {
    font-size: 18px;
    font-weight: 300;
    font-family: Montserrat;
    //max-height: 10%;
    padding: 5%;
}

.k-game-content-area__content {
    flex-grow: 1;
    padding: 5%;
}

.k-game-content-area__tray {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px;
    height: 10%;
    width: 100%;
}

.k-pause-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 43px;
    background: rgba(0, 0, 0, 0.425);
    background: v-bind('cardBg');
    border: none;
    outline: none;
    color: white;
    font-size: 24px;
    font-family: MenuFont;
    border-radius: 5px;
    pointer-events: all;
}

.k-skip-btn {
    height: 43px;
    background: rgba(0, 0, 0, 0.425);
    background: v-bind('cardBg');
    border: none;
    outline: none;
    color: white;
    font-size: 24px;
    border-radius: 5px;
    pointer-events: all;
    width: 50px;
    font-size: 12px;
    margin-left: 10px;
}
