
.k-game {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    overflow: hidden;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: 282828;
    color: v-bind('theme.values.fontColor');
}

.k-game-content {
    flex-grow: 1;
    width: 100%;
}

.k-game-tray {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 7%;
    height: 10%;
    width: 100%;
}
