
.k-match-summary {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    pointer-events: all;
    font-family: Montserrat;
}

.k-match-summary__title {
    padding: 7%;
}

.k-match-summary__h1 {
    font-size: 34px;
    font-weight: 300;
    margin: 0;
    padding: 0;
    margin-bottom: 12px;
    letter-spacing: 1px;
}

.k-match-summary__h2 {
    margin: 0;
    padding: 0;
    font-size: 14px;
    font-weight: 300;
    letter-spacing: 1px;
}

.k-match-summary__content {
    flex-grow: 1;
    padding: 7%;
}

.k-match-summary__tray {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 7%;
    height: 10%;
    width: 100%;
}

.k-continue-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0,0,0,.425);
    background: v-bind('theme.theme.cardBg');
    border: none;
    border-radius: 5px;
    color: #fff;
    font-family: Montserrat;
    height: 43px;
    outline: none;
    pointer-events: all;
    width: 100%;
    font-size: 14px;
}

.k-error-message {
    display: flex; 
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    padding: 15px;
    background: rgba(0,0,0,.425);
    background: v-bind('theme.theme.cardBg');
    border-radius: 13px;
    margin-bottom: 27px;
}

.k-error-message__icon {
    margin-right: 10px;
    font-size: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: v-bind('theme.theme.titleBg'); //linear-gradient(90deg, #00FFF0 0%, #EB00FF 100%);
    background-clip: text;
    text-fill-color: transparent;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
