<template>
    <div class="k-color-text" :style="{ background: bgColor }">
        <ContrastText :color="color">
            <slot></slot>
        </ContrastText>
    </div>
</template>

<script setup lang="ts">
import { ref } from 'vue';
import chroma from 'chroma-js';
import ContrastText from './contrast-text.vue';

const props = defineProps<{
    color: chroma.Color
}>()

const bgColor = ref(chroma(props.color).hex());
</script>

<style>
.k-contrast-color-text {
    padding: 20px;
    border-radius: 4px;
    text-align: center;
    font-weight: 500;
    background: v-bind('bgColor');
}
</style>