
.k-gradient-shape {
    display:flex;
    flex-wrap:wrap; 
    width:100%; 
    //overflow: hidden; 
    border-radius: 10px;
}

.k-gradient-shape__item {
    width: v-bind('tileWidth');
    aspect-ratio: 1 / 1;
}

.k-sortable-ghost {
    opacity: 1 !important;
}

.k-sortable-drag {
    opacity: 1 !important;
}

//The class of the dragged element (in its original position, while being dragged)
.k-sortable-chosen {
    opacity: .25 !important;
}

//The class of the element that is being swapped with the dragged element (when hovering over it)
.sortable-swap-highlight {
    opacity: .8 !important;
}
