
.k-color-text {
    padding: 20px;
    border-radius: 4px;
    text-align: center;
    font-weight: 500;
    position: relative;
}

.k-gradient-bg {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: flex;
    overflow: hidden;
    border-radius: 4px;
}

.k-gradient-bg__step {
    flex-grow: 1;
    height: 100%;
    z-index: -1;
}

.k-color-grid {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
}

.k-pick-color-btn {
    aspect-ratio: 1/1;
    width: 20%;
    margin: 3px;
    margin-bottom: 17px;
}
