
.au-theme-menu__info {
    margin: 0px 30px 23px 30px;
    font-size: 12px;
}

.au-theme-menu__list-wrapper {
    width: 100%;
    flex-grow: 1;
    overflow-y: auto;
    padding: 0 30px;
}

.au-theme-list {
    margin: 0;
    padding: 0;
    list-style: none;
    width: 100%;
    max-height: 100%;
    overflow-y: auto;
    pointer-events: all;
}

.au-theme-list__item {
    width: 100%;
    border-radius: 3px;
    padding: 22px;
    margin-bottom: 10px;
    background: transparent;
    position: relative;
    overflow: hidden;
    font-family: Montserrat;
    letter-spacing: 1px;
}

.au-theme-list__item-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.au-theme-list__item-name {
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.au-theme-list__item-preview {
    background: #fff;
    border-radius: 5px;
    height: 16px;
    width: 16px;
    margin-right: 10px;
}

.au-theme-list__item--selected {
    color: black;
    font-weight: bold;
