.au-theme-list__unlock-list {
    display: none;
    font-size: 12px;
    list-style: none;
    margin: 10px 0 0 0;
    padding: 0px;
    //padding: 0 0 0 10px;
}

.au-theme-list__unlock-item {
    margin-bottom: 5px;
    //font-family: Montserrat;
}

.au-theme-list__item--locked {
    //pointer-events: none;
    opacity: .5;
    .au-theme-list__unlock-list {
        display: block;
    }
}

.au-theme-list__item-bg {
    left: 0;
    top: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    // background: white;
    // opacity: .4;
    background: v-bind('themeStore.theme.cardBg');
    z-index: -1;
}

.au-slide-popup {
    width: 100%;
    background: transparent;
    position: absolute;
    left: 0;
    opacity: 0;
}
