<template>
    <div ref="container" class="k-countdown-seconds-container">
        <div ref="countEl"> {{ secondsRemaining }} </div>
    </div>
</template>

<script setup lang="ts">
import { onMounted, ref, computed, watch, UnwrapRef } from 'vue';
import { useMotion } from '@vueuse/motion';
import { useCountdownClock } from 'util/composables';
import { useModule } from 'src/modules/modules';

const { game, theme } = useModule();

const { count, autostart = true, delay = 0 } = defineProps<{
    count: number,
    autostart: boolean,
    delay?: number
}>();

const emit = defineEmits(['end']);
const clock = useCountdownClock(game.gameScene as any); //Fuck vue
const secondsRemaining = computed(() => {
    let sec = Math.floor(clock.remaining.value / 1000);
    if (clock.remaining.value === 0){
        return '';
    }
    return `${sec + 1}`;
})

const container = ref<HTMLDivElement>();
const containerMotion = useMotion(container, {
    initial: {
        scale: 0,
        opacity: 0,
        y: 300
    },
    enter: {
        scale: 1,
        opacity: 1,
        y: 0,
        transition: {
            delay: 300,
            duration: 500,
            ease: 'easeOut'
        }
    },
    leave: {
        scale: 0,
        opacity: 0,
        transition: {
            duration: 500,
            ease: 'easeOut'
        } 
    }
})

const countEl = ref<HTMLDivElement>();
watch(() => secondsRemaining.value, () => {
    useMotion(countEl, {
        initial: {
            scale: 0,
            opacity: 0
        },
        enter: {
            scale: 1,
            opacity: 1,
            transition: {
                duration: 350,
                ease: 'easeOut'
            }
        },
    })
})

onMounted(() => {
    if (autostart){
        setTimeout(() => {
            clock.start(count * 1000);
        }, delay);
    }
})

clock.on('end', () => {
    emit('end');
});

defineExpose({
    start: () => {
        if (clock.status.value !== 'running'){
            clock.start(count * 1000);
        }
    },
    leave: () => {
        clock.stop()
        return containerMotion.apply('leave');
    }
})
</script>

<style>
.k-countdown-seconds-container {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 36px;
    font-weight: 300;
    letter-spacing: 1px;
    background: v-bind('theme.theme.cardBg');
    border-radius: 100%;
    padding: 10px;
    width: 100px;
    aspect-ratio: 1 / 1;
}
</style>