
.k-color-fact-container {
    padding: 7%;
    width: 100%;
    display: flex;
    justify-self: flex-start;
    align-items: center;
    flex-direction: column;
    pointer-events: all;
}

.k-color-fact-title {
    font-size: 22px;
    font-weight: 500;
    margin-bottom: 1rem;
    margin-top: 0px;
    text-align: left;
    width: 100%;
    font-family: Montserrat;
    background: v-bind('theme.theme.titleBg'); //linear-gradient(90deg, #00FFF0 0%, #EB00FF 100%);
    background-clip: text;
    text-fill-color: transparent;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.k-color-fact-posttext {
    display: block;
    font-size: 22px;
    font-weight: 500;
    margin-bottom: 0px;
    margin-top: 1rem;
    text-align: left;
    width: 100%;
    font-family: Montserrat;
    background: v-bind('theme.theme.titleBg'); //linear-gradient(90deg, #00FFF0 0%, #EB00FF 100%);
    background-clip: text;
    text-fill-color: transparent;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.k-color-fact-text {
    width: 100%;
    background: #00000054;
    padding: 20px;
    border-radius: 5px;
    font-weight: 300;
    line-height: 29px;
}
