
.k-score-error-message {
    background: rgba(0,0,0,.329);
    border-radius: 5px;
    font-weight: 300;
    line-height: 29px;
    padding: 20px;
    width: 100%;
    margin: 7%;
    pointer-events: all;
}

.k-error-text {
    font-size: 22px;
    font-weight: 500;
    margin-top: 0px;
    text-align: left;
    width: 100%;
    font-family: Montserrat;
    background: v-bind('theme.theme.titleBg'); //linear-gradient(90deg, #00FFF0 0%, #EB00FF 100%);
    background-clip: text;
    text-fill-color: transparent;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
