
    .k-name-picker {
        list-style-type: none;
        padding: 0;
        margin: 0;
        width: 70%;
    }

    .k-name-picker-item {
        margin-bottom: 10px;
        width: 100%;
        border-radius: 4px;
        overflow: hidden;
    }

    .k-name-picker-btn {
        background: v-bind('theme.theme.cardBg');
        color: white;
        width: 100%;
        height: 100%;
        padding: 15px;
        border: none;
    }
