
    .au-modal-container {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;
        width: 100vw;
        height: 100vh;
        backdrop-filter: blur(5px);
        --webkit-backdrop-filter: blur(5px);
        pointer-events: all;
    }

    .au-modal {
        //position: absolute;
        // top: 50%;
        // left: 50%;
        // transform: translate(-50%, -50%);
        width: 80%;
        //min-height: 50%;
        height: auto;
        background: rgba(0, 0, 0, .85);
        border-radius: 25px;
        font-family: Montserrat;
        padding: 10px;
        padding-bottom: 20px;
    }

    .au-modal__title {
        height: 30px;
        text-transform: uppercase;
        letter-spacing: 1px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #ffffff1f;
        border-radius: 110px;
        
    }

    .au-modal__content {
        padding: 25px 5px
    }

    .au-modal__footer {
        align-items: center;
        bottom: 20px;
        display: flex;
        font-family: MenuFont;
        justify-content: flex-end;
        width: 100%;
        padding-left: 10px;
        padding-right: 10px;
        padding-bottom: 0px;
    }

    .au-modal__cancel {
        color: white;
        background: #ffb00036;
        outline: none;
        border: none;
        border-radius: 5px;
        padding: 10px;
    }

    .au-modal__dismiss {
        color: white;
        background: #ffffff36;
        outline: none;
        border: none;
        border-radius: 5px;
        padding: 10px;
    }


    


