


.k-radial-picker {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    overflow: hidden;
}

.k-radial-picker__tier {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    overflow: hidden;
    border-radius: 100%;
}

.tier-1 {
    aspect-ratio: 1/1;
    width: 100%;
    // height: 500px;
    border-radius: 100%;
}

.tier-2 {
    aspect-ratio: 1/1;
    width: 60%;
    border-radius: 100%;
}

.k-radial-picker__ring {
    width: 100%;
    height: 100%;
    display: block;
    border-radius: 100%;
    overflow: hidden;
    position: absolute;
}

.k-radial-picker__item-container {
    top: 50%;
    width: 100%;
    height: 100%;
    text-align: center;
    transform-origin: 50% 0;
    position: absolute;
    pointer-events: none;
    line-height: 100px;
    font-size: 2em;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.k-radial-picker__item {
    display: inline-block;
    margin: 0 auto;
    width: 50px;
    height: 50px;
    line-height: 50px;
    background: #fff;
    border-radius: 50%;
    pointer-events: all;
}
