
.k-round-summary {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-bottom: 375px;
}

.k-round-title {
    // font-size: 34px;
    // font-weight: 300;
    // letter-spacing: 10px;
}

.k-round-title__preheader {
    letter-spacing: 1px;
    font-size: 14px;
    font-weight: 300;
    margin-bottom: 6px;
    opacity: .8;
}

.k-round-title__header {
    font-size: 34px;
    font-weight: 300;
    letter-spacing: 10px;
}

.k-round-score {
    letter-spacing: 2px;
}

.k-round-score__multipliers {
    font-weight: 300;
    text-align: left;
    //margin-left: -9px;
}

.k-round-score__multipliers {
    transform-origin: center left;
}

.k-round-skip-message {
    position: absolute;
    bottom: 5%;
    font-weight: 300;
}
