/**
 * This is an adjustment class which can be applied to elements which should cover the entirety of the screen,
 * not just the "content" area. This is has an effect on iOS mobile devices which have a "safe area" inset for
 * a notch of otherwise. This class basically reverses the adjustment currently applied to the `k-control-overlay`
 * class.
 */
.absolute-full-cover {
    //Uncomment to test and apply a "fake" inset top position of 100px on k-control-overlay
    // top: -100px !important;
    // height: calc(100% + 100px) !important;
    top: calc(0px - env(safe-area-inset-top)) !important;
    height: calc(100% + (env(safe-area-inset-bottom) + env(safe-area-inset-top))) !important;
}

.ios-inset-padding {
    padding-top: env(safe-area-inset-top);
    padding-bottom: env(safe-area-inset-bottom);
}

@media (max-width: 400px){
    html, body {
        font-size: 14px;
    }
}

