
.k-color-text {
    padding: 20px;
    border-radius: 4px;
    text-align: center;
    font-weight: 500;
}

.k-color-grid {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
}

.k-pick-color-btn {
    aspect-ratio: 1/1;
    width: 20%;
    margin: 3px;
    margin-bottom: 17px;
}
