
@import '../styles/mixins.scss';

$title-height: 75px;
$nav-height: 115px;

.au-navigable {
   @include menu-screen();
}

.au-navigable__header {
    width: 100%;
    padding: 20px 30px 0 30px;
    height: $title-height;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.au-navigable__title {
    font-family: TitleFont;
    font-size: 24px;
    font-weight: 300;
    text-transform: uppercase;
    letter-spacing: 1px;
    pointer-events: all;
    flex-grow: 1;
}

.au-navigable__header-info {
    flex-grow: 1;
    text-align: right;
    padding-top: 12px;
}

.au-navigable__content {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    height: calc(100% - #{$nav-height + $title-height});
}

.au-navigable__nav {
    font-family: MenuFont, sans-serif;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 25px;
    width: 100%;
    height: $nav-height;
}
