
.k-done-btn {
    align-items: center;
    aspect-ratio: 1/1;
    background: hsla(0,0%,100%,.288);
    border: 4px solid #fff;
    border-radius: 100%;
    box-shadow: none;
    display: flex;
    justify-content: center;
    outline: none;
    position: relative;
    color: white;
    font-family: 'Montserrat';
    font-size: 25px;
    font-weight: 200;
    width: v-bind('$props.width');
}

.k-done-btn__label {
    margin: 0;
    padding: 0;
    font-size: 1rem;
}

.k-done-btn__ring {
    background: transparent;
    border: 1px solid #fff;
    position: absolute;
    width: 90%;
    height: 90%;
    border-radius: 100%;
}

