
.k-flash-overlay {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
    pointer-events: none;
    background: #ffffff;
    opacity: 0;
}

.k-drop-zones {
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.k-drop-zone-row {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.k-drop-zone {
    aspect-ratio: 1/1;
    background: #00000069;
    background: v-bind('theme.theme.cardBg');
    transition: background 100ms;
    opacity: .75;
    width: 30%;
    margin-bottom: 5%;
    border-radius: 5px;
    overflow: hidden;
}

.k-drop-zone-fill {
    width: 100%;
    height: 100%;
}

/**
 * Class applied to potential drop targets when a draggable is dragged over them. 
*/
.k-drop-zone--hover {
    background: rgba(255, 255, 255, 0.219);
    transition: background 100ms;
}

.k-pattern-mem-bottom {
    width: 100%;
    height: 20%;
    margin-top: 20px;
}

.k-draggables-container {
    -ms-touch-action: none;
    touch-action: none;
    width: 80%;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    pointer-events: all;
}

.k-pattern-memory-draggable {
    -ms-touch-action: none;
    touch-action: none;
    width: 100px;
    width: 20%;
    aspect-ratio: 1 / 1;
    border-radius: 3px;
    transition: width, height 250ms;
    margin-right: 15px;
    margin-bottom: 15px;
    //position: absolute;
}

.k-countdown-duration {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
