<template>
    <ColorPhasingElement 
        ref="phaseRef"
        v-bind="$attrs"
        :targetColor="props.targetColor"
        :steps="props.steps"
        class="k-phase-bg absolute-full-cover">
    </ColorPhasingElement>
</template>

<script setup lang="ts">
import { ref } from 'vue';
import * as GameTypes from 'types/game.types';
import ColorPhasingElement from './color-phasing-element.vue';

const props = defineProps<{
    /**
     * The target color, which is only used to ensure that any step w/ the color set to 'random'
     * will be a color that is sufficiently different from the target color.
     */
    targetColor: chroma.Color,
    /**
     * The color phase steps
     */
    steps: GameTypes.PhaseStep[],
}>()

const phaseRef = ref();
defineExpose({
    stop: () => phaseRef.value.stop()
})
</script>

<style scoped>
.k-phase-bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
}
</style>