@mixin menu-screen(){
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    pointer-events: none;
}

@mixin flex-centered(){
    display: flex;
    justify-content: center;
    align-items: center;
}