
body {
    color: v-bind('theme.theme.fontColor');
}

.k-menu {
    position: relative;
    align-items: center;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    pointer-events: none;
    width: 100%;
    height: 100%;
    font-family: MenuFont;
}

.k-menu__title {
    pointer-events: all;
    //font-weight: 300;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20vh;
    font-family: Montserrat;
    font-size: clamp(14px, 102px, 12vw);
    background: v-bind('theme.theme.titleBg'); //linear-gradient(90deg, #00FFF0 0%, #EB00FF 100%);
    background-clip: text;
    text-fill-color: transparent;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    letter-spacing: 7px;
    img {
        width: 70%;
    }
}

.k-menu__play {
    font-family: TitleFont;
    margin-bottom: 20vh;
}

.k-menu__opts {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 75%;
    margin-bottom: 7.5vh;
}

.k-menu__remove-ads {
    width: 75%;
}

.audio-toggle-pos {
    position: absolute;
    left: 15px;
    top: 15px;
    //top: calc(15px + env(safe-area-inset-top)); //For iOS notch
}

.k-color-fact-btn {
    font-family: 'Montserrat';
    position: absolute;
    top: 18px;
    //top: calc(18px + env(safe-area-inset-top)); //For iOS notch
    right: 15px;
    background: #ffffff1c;
    padding: 5px;
    border-radius: 5px;
    font-size: 12px;
    border: none;
    outline: none;
    color: white;
    pointer-events: all;
}

