
.k-game-over {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1000;
    width: 100%;
    pointer-events: all;
}

.k-game-over__content {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 375px;
    flex-direction: column;
}

.k-gameover-title {
    font-size: 30px;
    font-weight: 300;
}
