
.k-color-text {
    padding: 20px;
    border-radius: 4px;
    text-align: center;
    font-weight: 500;
}

.k-tap-gradient-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.k-tap-gradient-colors {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    flex-grow: 1;
}

.k-tap-gradient-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 12%;
    margin-bottom: 1px;
    font-size: 20px;
    font-weight: 500;
    border-radius: 0px;
    &:first-child {
        border-radius: 4px 4px 0 0;
    }

    &:last-child {
        border-radius: 0 0 4px 4px;
    }
}
