
.k-color-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.k-single-color-btn--square {
    aspect-ratio: 1/1;
    width: 50%;
    border-radius: 4px;
    background: v-bind('$props.colors[0].hex()');
}

.k-single-color-btn--circle {
    aspect-ratio: 1/1;
    width: 60%;
    border-radius: 100%;
    background: v-bind('$props.colors[0].hex()');
}
