
.au-menu-btn {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: MenuFont;
    background-color: transparent;
    border: none;
    outline: none;
    color: white;
    width: 75px;
    pointer-events: auto;
    cursor: pointer;
    transition: 150ms;
    transform: scale(1);
    &:hover {
        transition: 150ms;
        transform: scale(1.1);
    }
}

.au-menu-btn__icon {
    font-size: 26px;
    margin-bottom: 10px;
}
