
.au-glow-btn {
    align-items: stretch;
    /* border: 2px solid #fff; */
    border-radius: 3px;
    color: #fff;
    cursor: pointer;
    display: inline-flex;
    font-family: Montserrat,sans-serif;
    font-size: .775rem;
    outline: none;
    padding: .75rem 1rem;
    pointer-events: auto;
    position: relative;
    z-index: 0;
    border: none;
    outline: none;
    background: #ffffff21;
}

.au-glow-btn__icon {
    font-size: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 20px;
}

.au-glow-btn__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    text-align: left;
}

.au-glow-btn__label {
    font-size: 1rem;
}

.au-glow-btn__desc {
    font-size: .775rem;
    opacity: .75;
}

