@keyframes shimmer-bg {
    0% { background: #ffffff; }
    15% { background: #ff8484; }
    30% { background: #84ffb5; }
    45% { background: #cd84ff; }
    60% { background: #68ffdb; }
    75% { background: #ff6868; }
    90% { background: #bbdbff; }
}

@keyframes shimmer-border {
    0%  { border-color: #ffffff; }
    15% { border-color: #ff8484; }
    30% { border-color: #84ffb5; }
    45% { border-color: #cd84ff; }
    60% { border-color: #68ffdb; }
    75% { border-color: #ff6868; }
    90% { border-color: #bbdbff; }
}