<template>
    <div ref="container" class="k-pointer-fx-container"></div>
</template>

<script setup lang="ts">
import { onBeforeUnmount, onMounted, ref } from 'vue';
import { useMotion } from '@vueuse/motion'
import chroma from 'chroma-js';

const { colorStart = '#ffffff', colorEnd = '#ffffff' } = defineProps<{ 
    colorStart?: string, 
    colorEnd?: string 
}>();

// let colorStart = '#ffffff';
// let colorEnd = '#ffffff';

/**
 * Creates a `div` element with class `k-pointer-fx` at the pointer event position and 
 * then begins a transition from `colorStart` to `colorEnd` over `duration` milliseconds,
 * as well as increasing the scale from 0 to 1 over the same duration.
 */
const container = ref<HTMLElement>();
const stopMotions = new Set<Function>();
const pointerEls = new Set<HTMLElement>();
const handleDocumentPointerDown = (e: PointerEvent) => {
    let el = document.createElement('div');

    let containerRect = container.value?.getBoundingClientRect();
    if (containerRect == null) return;

    el.classList.add('k-pointer-fx');
    el.style.left = `${(e.pageX - containerRect?.left) - 50}px`;
    el.style.top = `${(e.pageY - containerRect?.top) - 50}px`;
    el.style.backgroundColor = chroma(colorStart).hex();
    el.style.transform = 'scale(0)';
    container.value?.appendChild(el);

    pointerEls.add(el);
    const { apply, stop } = useMotion(el, {
        initial: {
            opacity: 1,
            scale: 0,
            backgroundColor: chroma(colorStart).hex()
        },
        enter: {
            opacity: 0,
            scale: 5,
            backgroundColor: chroma(colorEnd).hex(),
            transition: {
                duration: 350,
                ease: 'easeOut',
                onComplete: () => stopMotions.delete(stop)
            }
        }
    });
    stopMotions.add(stop);
    apply('enter');
}

onMounted(() => document.addEventListener('pointerdown', handleDocumentPointerDown));
onBeforeUnmount(() => {
    stopMotions.forEach(stop => stop());
    pointerEls.forEach(el => container.value?.removeChild(el));
    document.removeEventListener('pointerdown', handleDocumentPointerDown)
})
</script>

<style>
.k-pointer-fx-container {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 500;
    pointer-events: none;
}

.k-pointer-fx {
    width: 100px;
    height: 100px;
    border-radius: 100%;
    position: absolute;
    pointer-events: none;
}
</style>