<template>
    <div class="flex-centered" style="position:absolute; width:100%; height:100%; top:0; left:0; pointer-events: none;">
        <div ref="circle" class="k-phase-bg-leave"></div>
    </div>
</template>

<script setup lang="ts">
import { ref } from 'vue';
import { useMotion } from '@vueuse/motion'
import { useModule } from 'src/modules/modules';

const { theme } = useModule();

/**
 * Leave transition
 */
const circle = ref();
const circleMotion = useMotion(circle, {
    initial: {
        opacity: 0,
        scale: 0
    },
    leave: {
        opacity: 1,
        scale: 30,
        transition: {
            duration: 500,
            ease: 'easeOut'
        }
    }
})

defineExpose({
    leave: () => circleMotion.apply('leave')
})
</script>

<style>
.k-phase-bg-leave {
    width: 50px;
    height: 50px;
    z-index: 100;
    border-radius: 100%;
    background: rgb(30, 36, 48);
    background: v-bind('theme.theme.gameBg');
    pointer-events: none;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1px;
}
</style>