
.k-rank-meter {
    position: relative;
    height: 100%;
    flex-grow: 1;
    width: 100%;
}

.k-rank-meter__meter {
    position: absolute;
    left: 0;
    top: 0;
    width: 25px;
    height: 100%;
    border-radius: 3px;
    background: linear-gradient(#00FF47, #00D1FF, #DB00FF, #FF9900, #FF0000);
}

.k-rank-meter__pos {
    position: absolute;
    left: 28px;
    opacity: 0;
    top: v-bind('meterPos');
}

.k-rank-meter__content {
    //display: none;
    position: absolute;
    //width: calc(100% - 38px);
    width: calc(100% - 60px);
    padding: 10px;
    background: rgba(255, 255, 255, 0.116);
    border-radius: 3px;
    font-weight: 300;
    opacity: 0;
    overflow: hidden;
}

.k-rank-meter__player-color {
    display: inline-block;
    font-size: 16px;
    margin-bottom: 10px;
    overflow: visible;
}

.k-rank-meter__player-stats {
    font-size: 14px;
}
